import React, { useState } from 'react';
import { Detail, DetailBlock } from '../../core/viewmodel/Details';
import DetailTexture from './DetailTexture';

interface DetailArgs{
    setDetail: (arg0: any) => any;
    detail: DetailBlock;
    current: Detail
    width: number;
    height: number;
}

function DetailComponent(props: DetailArgs) {

    //console.log("__selection", props.current.IsSelected(), props.current === props.current.parent.parent.parent.selection)

    const [selectBool, setSelectBool]=useState(); 
    
    const update = () => props.setDetail(props.detail.Clone());

    const select = () => {
         props.current.Select(props.detail);
         update();
         console.log();
    }

    const cut = () => {
        props.current.Cut(props.detail);
        
        // props.current.parent.details[0].Select(props.detail);
        update();
    }

    const remove = () => {
        props.current.Delete(props.detail);
        console.log("__remove", props.detail.selection);
        update();
    }
    
    let texture = Object.values(props.current.properties.materials)[0].textureURL;


    return (
        <div onClick={select} style={{ width: `${props.width}rem`, height: `${props.height}rem` }} className={'detail' + (props.current.IsSelected(props.detail) ? ' selected' : '')}>

            <DetailTexture props={props.current.properties}></DetailTexture>


            {props.current.IsSelected(props.detail) === false && <div className='fader' ></div>}
            <div onClick={e=>e.stopPropagation()}>
            {
                (props.current.IsSelected(props.detail) && props.current.CanDelete()) &&
                <button id='fa-right' className='floatingAction' onClick={remove}>
                    <span className="material-icons"> close </span>
                </button>
            }

            {
                (props.current.IsSelected(props.detail) && props.current.CanCut()) &&
                <button id='fa-center' className='floatingAction' onClick={cut}>
                    <span className="material-icons"> content_cut </span>
                </button>
            }
            </div>
        </div>
    );
}

export default DetailComponent;