/**
 *  2  8000  4000   
 * @returns hblock
 */
export const hblock_8000x4000 = () => {
  //  8000  4000
  const points: Vector2[] = [
    new Vector2(0, 0),
    new Vector2(8000, 0),
    new Vector2(8000, 4000),
    new Vector2(0, 4000),
  ];
  let hblock = new Hblock(points, "test hblock", 2, 2000, 1000, 1000);

  return hblock;
};

