const hblocks = (index: number) => {
  let file = JSON.parse(json)
  let jsonHblock = file.hblocks[index] as Hblock
  const testHblock = Object.assign(new Hblock([]), jsonHblock);
  testHblock.points = [];
  jsonHblock.points.forEach(p => testHblock.points.push(new Vector2(p.x, p.y)));
  
  //console.log(testHblock)
  return testHblock
}

