import React from 'react';
import { Material } from '../../../../core/model/Interfaces';

export interface Btns_MaterialProps {
    materials: Material[] | undefined
    onSelectMaterialintoModal: any
    selectedMaterial: Material | undefined
  }

const Btns_Materials = ({ materials, selectedMaterial, onSelectMaterialintoModal}: Btns_MaterialProps) => {
    return (
        <div className='btns_materials' key='btns_materials'>

            {materials?.map((material: Material, index: number) => {
              if (material.iconURL === null || materials === null) return;
              // console.log(materials[key].iconURL);
              return (
                <div className='materialLableSelector' key={index}>

                  <button
                    id={material === selectedMaterial ? 'materialActive' : ''}
                    disabled={material === selectedMaterial ? true : false}
                    onClick={() => onSelectMaterialintoModal(material)}
                    className='myButtonMaterialSelect'
                    key={material.iconURL + "_btn"}
                    style={{
                      backgroundImage: `url(${material.iconURL})`,
                      backgroundSize: 'cover',
                      backgroundRepeat: 'no-repeat',
                      backgroundPosition: 'center',
                    }}
                  >
                  </button>
                  <label id='textImage' key={material.iconURL + "textImage"}> {material.name}</label>
                </div>

              )
            })}
          </div>
    );
};

export default Btns_Materials;