import React, { useEffect } from 'react';
import { CSSTransition } from "react-transition-group";
import ReactDOM from "react-dom";
import { Material } from '../../../../core/model/Interfaces';
import Modal from "react-modal";
import './MyModal.css'
import Tabs from '../Tab/Tabs';
import Btns_Materials from './Btns_Materials';


export interface ModalProps {
  toggleModal: any
  isOpen: boolean
  materials: Material[] | undefined
  selectMaterialType: string | undefined
  onSelection: any
  selectedIndex: number | undefined
}
Modal.setAppElement("#root");
const MyModal = ({ toggleModal, isOpen, materials, selectMaterialType, onSelection, selectedIndex }: ModalProps) => {

  function onSelectMaterialintoModal(material: Material) {
    onSelection(selectMaterialType, material);
    toggleModal();
  }

  const groupByCategory = materials?.reduce((group, product) => {
    const { type } = product;
    group[type!] = group[type!] ?? [];
    group[type!].push(product);
    return group;
  }, {})
  // console.log(groupByCategory);
function getMaterialTypeName()
{
  console.log();
  return materials!= undefined? materials![selectedIndex!].type:"undefined";
}
  


  return (
    <div>
      <Modal
        isOpen={isOpen}
        onRequestClose={toggleModal}
        contentLabel="My dialog"
        className="mymodal"
        overlayClassName="myoverlay"
        closeTimeoutMS={500}
        key={"My dialog"}
      >
        <a href="#" key={"close"} className="close" onClick={() => toggleModal()}></a>
        <div  className='modalContent'>
          {/* <h5 style={{ marginBottom: '1rem' }}> :</h5> */}


          {groupByCategory!=undefined &&
                <Tabs>
                {Object?.keys(groupByCategory!)?.map((key: string, index: number) => {
                   
                    return (
                      <div label={key} activeTab={  getMaterialTypeName()}  key={index}>
                        <Btns_Materials materials={groupByCategory![key]} onSelectMaterialintoModal={onSelectMaterialintoModal} selectedMaterial={ materials!= undefined? materials![selectedIndex!]:undefined} />
                      </div>
                    )
                  })}
                </Tabs>
            }
          
         
        </div>
      </Modal>
    </div>
  );
};

export default MyModal;