import React, { useEffect, useState } from 'react';
import TypeSelector from './styleEditor/TypeSelector';

import { ModulePartType, PatternType } from "../../core/Enums";
import { FacadeModulePart, Material } from '../../core/model/Interfaces';
import { Materials } from '../../core/data/Materials';
import { facadeSubtypes } from '../../core/data/facadeSubtypes';
import { TypeIcons } from '../../core/const';
import MaterialSelector from './styleEditor/MaterialSelector';
import SizeEditor from './styleEditor/SizeEditor';
import './StyleEditor.css'


export default function StyleEditor({ modulePart, handleModulePartChange , lockHeightSlider}) {

 
  let images = getTypeImages();
  function getTypeImages()
  {
    if(modulePart.type===ModulePartType.FIRSTFLOOR)
    {
      return TypeIcons.filter(o => o.type===ModulePartType.FIRSTFLOOR).map(i=>i.icon);
    }
    else if(modulePart.type===ModulePartType.PARAPET)
    {
      return TypeIcons.filter(o => o.type===ModulePartType.PARAPET).map(i=>i.icon);
    }
    else{
      return TypeIcons.filter(o => o.type!=ModulePartType.PARAPET && o.type!=ModulePartType.FIRSTFLOOR).map(i=>i.icon);
    }
  }

  let typeObject = TypeIcons.filter(o => o.type === modulePart.type)[0]
  let typeIndex = TypeIcons.indexOf(typeObject)

  function onTypeSelection(index: number) {
    let selectedType = TypeIcons[index]
    if (modulePart.type === selectedType.type) return;

    let nextSubtype = Object.values(facadeSubtypes).filter(s => s.modulePartType === selectedType.type)[0]
    let materialTypes = nextSubtype.geometryParts.map(p => p.materialSources[0])
    let nextMaterials = materialTypes.map(mt => Object.values(Materials).filter(m => m.materialType === mt)[0])

    let nextMat: { [x: string]: Material } = {}
    nextSubtype.geometryParts.forEach((p, index) => {
      nextMat[p.meshName] = nextMaterials[index];
    })

    let nextModulePart = {
      ...modulePart,
      type: selectedType.type,
      subtype: nextSubtype,
      materials: nextMat,
    } as FacadeModulePart

    correctSizes(nextModulePart);
    handleModulePartChange(nextModulePart);

  }

  let subElements = Object.values(facadeSubtypes).filter(s => s.modulePartType === modulePart.type);
  let subImages = subElements.map(v => v.icon);
  let subTypeIndex = subElements.indexOf(modulePart.subtype);

  function onSubTypeSelection(index: number) {
    let nextSubtype = subElements[index]
    if (modulePart.subtype === nextSubtype) return;


    let materialTypes = nextSubtype.geometryParts.map(p => p.materialSources[0])
    let nextMaterials = materialTypes.map(mt => Object.values(Materials).filter(m => m.materialType === mt)[0])

    let nextMat: { [x: string]: Material } = {}
    nextSubtype.geometryParts.forEach((p, index) => {
      nextMat[p.meshName] = nextMaterials[index];
    })
    // Object.keys(nextMat).map(key=> {
    //   if(key in modulePart.materials)
    //   {
    //     nextMat[key]=modulePart.materials[key];
    //   }
    // })




    let nextModulePart = {
      ...modulePart,
      subtype: nextSubtype,
      materials: nextMat,
    } as FacadeModulePart

    correctSizes(nextModulePart);

    handleModulePartChange(nextModulePart);

  }

  function onTypeMaterialSelection(keySelectMaterial: string, material: Material) {

    let nextMat: { [x: string]: Material } = {}

    Object.keys(modulePart.materials).forEach((key) => {
      if (keySelectMaterial === key) {
        nextMat[key] = material;
      } else {
        nextMat[key] = modulePart.materials[key];
      }
    })


    let nextModulePart = {
      ...modulePart,
      materials: nextMat,
    } as FacadeModulePart

    handleModulePartChange(nextModulePart)
  }

  return (
    <div id='side-bot-element'>
      <div className='typeSelector'>
        <TypeSelector images={images} selectedIndex={typeIndex} onSelection={onTypeSelection} typeActiveId={'typeActiveMainTypes'}></TypeSelector>
        <SizeEditor modulePart={modulePart} setModulePart={handleModulePartChange}  lockHeightSlider={ lockHeightSlider} ></SizeEditor>
      </div>
      <div className='rectangleBackgroundSubTypeChange'></div>
      <div className='subTypeChange'>
      
        <TypeSelector images={subImages} selectedIndex={subTypeIndex} onSelection={onSubTypeSelection} typeActiveId={'typeActive'}></TypeSelector>
      </div>
      <MaterialSelector subType={modulePart.subtype} onSelection={onTypeMaterialSelection} materials={modulePart.materials}></MaterialSelector>
    </div>
  );
}

/**       
 *    ,  300,     ,
 *           .
*/
function correctSizes(nextModulePart: FacadeModulePart) {
  // throw new Error('Function not implemented.');
}

