import React, { useCallback, useState } from 'react';
import { sizeRules } from '../../../core/data/sizeRules';
import { ModulePartType } from '../../../core/Enums';
import { FacadeModulePart } from '../../../core/model/Interfaces';
import MyInput from './MyInputRange/MyInput';
import './SizeEditor.css'

export enum SizeType {
    width,
    height,
    depth,
  }

export interface sizeProps{
    modulePart: FacadeModulePart
    setModulePart:any
    lockHeightSlider:boolean
}


export default function SizeEditor({modulePart, setModulePart, lockHeightSlider}: sizeProps){

    const getSizeRules=Object.values(sizeRules).filter(s=> s.type==modulePart.type)[0];

    function onChangeSizesFromEnum(sizeType: SizeType, value:number){
       

        if(sizeType===SizeType.depth)
        {
            let nextModulePart = {
                ...modulePart,
                depth : value,
            } as FacadeModulePart
    
            setModulePart(nextModulePart)

        }
        if(sizeType===SizeType.height)
        {
            let nextModulePart = {
                ...modulePart,
                height: value
            } as FacadeModulePart
    
            setModulePart(nextModulePart)

        }
        if(sizeType===SizeType.width)
        {
            let nextModulePart = {
                ...modulePart,
                width : value,
            } as FacadeModulePart
    
            setModulePart(nextModulePart)

        }
       
    }

    return (
       
        <div className='inputSelectors'>
            <MyInput defaulValue={modulePart.width}  min={getSizeRules.width.min} max={getSizeRules.width.max}
           title={""} onSelected={onChangeSizesFromEnum} sizeType={SizeType.width}
           step={300}
           disabled={getSizeRules.width.disabled}/>
           
           <MyInput defaulValue={modulePart.height/100}  min={getSizeRules.height.min} max={getSizeRules.height.max}
           title={""} onSelected={onChangeSizesFromEnum} sizeType={SizeType.height}
           step={0.05}
           disabled={getSizeRules.height.disabled || lockHeightSlider}/>
           
           <MyInput defaulValue={modulePart.depth} min={getSizeRules.depth.min} max={getSizeRules.depth.max} 
           title={""} onSelected={onChangeSizesFromEnum} 
           sizeType={SizeType.depth} step={100}
           disabled={getSizeRules.depth.disabled}/>
        </div>
    );
};
