import * as THREE from "three";

export class Hblock {
    id: string;
    points: THREE.Vector2[];
    firstFloorHeight: number;
    typicalFloorHeight: number;
    parapetHeight?: number | undefined;
    totalFloors: number;
    facadeBottomLevels: number[];
    patternId?: string | undefined;

    constructor(points: THREE.Vector2[], id = '', totalFloors = 9, firstFloorHeight = 5200, typicalFloorHeight = 3000, parapetHeight = 1200, facadeBottomLevels: number[] = []) {
        this.id = id
        this.points = points
        this.totalFloors = totalFloors
        this.firstFloorHeight = firstFloorHeight
        this.typicalFloorHeight = typicalFloorHeight
        this.parapetHeight = parapetHeight

        if (facadeBottomLevels.length === 0) {
            this.facadeBottomLevels = Array(points.length).fill(1)
        } else {
            this.facadeBottomLevels = facadeBottomLevels
        }
    }

    /**  -  1.   + 1   .*/
    floorHeight(floorNumber: number) {
        if (floorNumber > (this.totalFloors + 1) || floorNumber < 1) {
            return null
        }

        if (floorNumber == 1) return this.firstFloorHeight
        if (floorNumber == this.totalFloors + 1) return this.parapetHeight

        return this.typicalFloorHeight
    }

    getFloorLevelHeight(floorNumber: number) {
        if (floorNumber > this.totalFloors + 1 || floorNumber < 1) {
            return null
        }

        if (floorNumber === 1) return 0

        return this.firstFloorHeight + (this.typicalFloorHeight * (floorNumber - 2))
    }

    totalHeight() {
        let parapet = this.parapetHeight ? this.parapetHeight : 1200;
        let height = this.firstFloorHeight + (this.typicalFloorHeight * (this.totalFloors - 1)) + parapet;
        return height;
    }
}