import React, { useState } from 'react';
import './TypeSelector.css'

interface SelectorProps{
    images : string[]| undefined,
    selectedIndex : number,
    onSelection: any,
    typeActiveId:string
}

const TypeSelector = ({images, selectedIndex, onSelection, typeActiveId} : SelectorProps) => {
    

    return (
        <div className='typeChange'>
            {images?.map((key : string, index : number) => {
                return (<button id={index === selectedIndex || images.length===1 ? typeActiveId : ''}
                    key={index}
                    className='myButton'
                    name={key} onClick={() => onSelection(index)}
                    style={ {backgroundImage: `url(${images[index]})`, backgroundSize: 'contain', backgroundRepeat: 'no-repeat', backgroundPosition: 'center'}}
                    disabled={images.length===1 ? true : false}
            >
             </button>)})}
        </div>
    );
};

export default TypeSelector;