import React, { Component, useState } from 'react';
import { SizeType } from '../SizeEditor';

import './MyInputRange.css'


export interface InputProps{
    defaulValue : number,
    min : number,
    max : number,
    title: string,
    onSelected: any
    sizeType: SizeType
    step:number
    disabled:boolean
}

export default function MyInput({defaulValue, min, max, title, onSelected ,sizeType, step , disabled}: InputProps){

    function chengeValue(value:string)
    {
        onSelected(sizeType, Number(value))
    }
    return (

            <div className='inputSelector'>
               
                <input type="range" 
                id="myInput"
                className="istyle"  
                value={defaulValue} 
                max={max} min={min} 
                onChange={e=> chengeValue(e.target.value)}
                step={step}
                disabled={disabled}
                />
                <label id='labelTittle'>{title}</label>
            </div>
            
    );
};