import React, { useEffect, useState } from 'react';
import { Modal, ModalBody } from 'reactstrap';
import Editor from './components/Editor';
import ActionBar from './components/editor/ActionBar';
import PatternPicker from './components/editor/PatternPicker';
import Sidebar from './components/Sidebar';
import MyModal from './components/sidebar/styleEditor/MyModal/MyModal';
import { HblocksJson } from './core/json/hblocks';
import { FacadeLayout } from './core/model/FacadeLayout';
import './custom.css';
import {PatternType} from './core/Enums'
import { pattern_1floor_parapet } from './core/data/testFacadePatterns';
import { Hblock } from './core/model/Hblock';
import { Vector2 } from 'three';

const testHblock = Object.assign(new Hblock([]), HblocksJson.hblocks[3]);
testHblock.points = [];
HblocksJson.hblocks[3].points.forEach(p => testHblock.points.push(new Vector2(p.x, p.y)));

function App(props) {
    const [hblock, setHblock] = useState({ pattern: pattern_1floor_parapet(), patternType: PatternType.CHECKED, hBlock: testHblock });
    const [facadeLayout, setFacadeLayout] = useState(null);
    const [patternIndex, setPatternIndex] = useState(0);
    const images = ['icons/patterns/regular.png', 'icons/patterns/checked.png', 'icons/patterns/doubleChecked.png', 'icons/patterns/random.png']

    const setPatternTypeAction = (e) => { 
        let index = images.indexOf(e)
        setHblock({ ...hblock, patternType: index })
        setPatternIndex(index)
    }
    const setPatternAction = (e) => { setHblock({ ...hblock, pattern: e }) }

    useEffect(() => {
        setTimeout(1000, () => {
            setFacadeLayout(new FacadeLayout(hblock.pattern, hblock.patternType, hblock.hBlock));
        })
    }, []);

    useEffect(() => {
        setFacadeLayout(new FacadeLayout(hblock.pattern, hblock.patternType, hblock.hBlock));
    }, [hblock]);


    return (
        <div>
            <Editor facadeLayout={facadeLayout} />
            <PatternPicker images={images} handleClick={setPatternTypeAction} patternType={patternIndex} />
            <Sidebar setPatternAction={setPatternAction} />
            <ActionBar />
        </div>
    );
}

export default App;