import { Pattern } from "./Pattern";
import { FacadeBlock } from "./FacadeBlock"
import { Hblock } from "./Hblock";
import { FacadeModule, FacadeModulePart, Location, Material } from "./Interfaces";
import * as THREE from "three";
import repeatAndScale from "../repeatAndScale";
import { Vector3 } from "three";
import { ModulePartType, PatternType } from "../Enums";
import { facadeSubtypes } from "../data/facadeSubtypes";
import { Materials } from "../data/Materials";

export class FacadeLayout {

    facadeBlocks: FacadeBlock[];
    roof: THREE.Object3D
    buttCaps: THREE.Object3D[]

    constructor(pattern: Pattern, patternType: PatternType, hblock: Hblock) {

        this.facadeBlocks = []
        this.buttCaps = []

        for (let i = 0; i < hblock.points.length; i++) {

            const startPoint = hblock.points[i]
            const endPoint = hblock.points[(i + 1) % hblock.points.length]

            //    
            if (hblock.facadeBottomLevels[i] === 0) {

                let hblockHeight = hblock.totalHeight()
                let wholeWallCap = createWallCap(startPoint, endPoint, hblockHeight)
                this.buttCaps.push(wholeWallCap)

                continue
            }

            //     1,     
            if (hblock.facadeBottomLevels[i] > 1) {

                let firstFloorHeight = hblock.firstFloorHeight
                let typicalFloorHeight = hblock.typicalFloorHeight
                let floorNumber = hblock.facadeBottomLevels[i]

                let capHeight = firstFloorHeight + (typicalFloorHeight * (floorNumber - 2))
                let wallCap = createWallCap(startPoint, endPoint, capHeight)
                this.buttCaps.push(wallCap)
            }

            for (let floorNumber = hblock.facadeBottomLevels[i]; floorNumber <= hblock.totalFloors; floorNumber++) {

                //         
                /*                 if (floorNumber == 1 && pattern.rows.find(row => row.rowType === "FirstFloor")) {
                                    let firstFloorModules = pattern.rows.filter(row => row.rowType === "FirstFloor")[0].modules
                
                                    const firstLevelHeight = hblock.getFloorLevelHeight(floorNumber)
                                    if (firstLevelHeight == null) throw Error()
                
                                    let floorHeight = hblock.floorHeight(floorNumber)
                                    if (floorHeight == null) throw Error()
                
                                    let coordinates = [i, floorNumber]
                                    let row = getFacadeBlockRow(firstFloorModules, startPoint, endPoint, firstLevelHeight, floorHeight, coordinates)
                                    this.facadeBlocks.push(...row)
                
                                    continue
                                } */

                const facadeModules = getFacadeModules(pattern, patternType, floorNumber)
                const levelHeigth = hblock.getFloorLevelHeight(floorNumber)
                if (levelHeigth == null) {
                    console.log('___hblock', hblock)
                    console.log('___ ', i)
                    console.log('___ ', floorNumber)
                    //throw Error("floorNumber = " + floorNumber)
                    continue
                } //     

                let floorHeight = hblock.floorHeight(floorNumber)
                if (floorHeight == null) throw Error() //     

                let coordinates = [i, floorNumber]
                let row = getFacadeBlockRow(facadeModules, startPoint, endPoint, levelHeigth, floorHeight, coordinates)
                this.facadeBlocks.push(...row)
            }

            //  .   
            if (pattern.rows.find(row => row.rowType === "Parapet")) {
                let parapetModules = pattern.rows.filter(row => row.rowType === "Parapet")[0].modules;

                let levelHeigth = hblock.getFloorLevelHeight(hblock.totalFloors + 1)
                if (levelHeigth == null) throw Error() //     

                let floorHeight = hblock.parapetHeight
                if (floorHeight == null) throw Error() //     

                let parapetRow = getFacadeBlockRow(parapetModules, startPoint, endPoint, levelHeigth, floorHeight, [i, hblock.totalFloors + 1])
                this.facadeBlocks.push(...parapetRow)
            }
        }

        // console.log(`   (${this.facadeBlocks.length}):`)
        // this.facadeBlocks.forEach(fb => console.log(fb.coordinates.join(';')));

        this.roof = createRoof(hblock)
    }
}

function createWallCap(startPoint: THREE.Vector2, endPoint: THREE.Vector2, capHeight: number): THREE.Object3D {

    let startPoint3d = new Vector3(startPoint.x, startPoint.y, 0).toArray()
    let endPoint3d = new Vector3(endPoint.x, endPoint.y, 0).toArray()

    let startUpPoint = new Vector3(startPoint.x, startPoint.y, capHeight).toArray()
    let endUpPoint = new Vector3(endPoint.x, endPoint.y, capHeight).toArray()

    //   - (   ,   )
    //https://threejs.org/docs/#api/en/core/BufferGeometry
    const pointsNumbers: number[] = []
    pointsNumbers.push(...startPoint3d, ...endPoint3d, ...startUpPoint, ...endPoint3d, ...endUpPoint, ...startUpPoint)

    const geometry = new THREE.BufferGeometry();
    const vertices = new Float32Array(pointsNumbers)

    geometry.setAttribute('position', new THREE.BufferAttribute(vertices, 3))
    const material = new THREE.MeshBasicMaterial({ color: 0xFFFFFF })
    const mesh = new THREE.Mesh(geometry, material)

    return mesh
}

function createRoof(hblock: Hblock): THREE.Object3D {

    const firstFloorHeight = hblock.firstFloorHeight
    const typicalFloorHeight = hblock.typicalFloorHeight
    const typicalFloorCount = hblock.totalFloors - 1
    const roofLocationHeight = firstFloorHeight + typicalFloorHeight * typicalFloorCount

    const roofShapePoints = hblock.points
    const roofShape = new THREE.Shape(roofShapePoints)
    const roofGeometry = new THREE.ShapeGeometry(roofShape)
    const roofMaterial = new THREE.MeshStandardMaterial({ color: 0xFFFFFF, wireframe: false })
    const roofMesh = new THREE.Mesh(roofGeometry, roofMaterial)

    const moveRoofUp = new THREE.Vector3(0, 0, roofLocationHeight)
    roofMesh.position.add(moveRoofUp)

    return roofMesh
}

function getFacadeModules(pattern: Pattern, patType: PatternType, floorNumber: number): FacadeModule[] {

    if (floorNumber == 1) return pattern.rows.filter(row => row.rowType === "FirstFloor")[0].modules

    let typicalFloorRows = pattern.rows.filter(row => row.rowType === "TypicalFloor");

    let rowIndex = 0

    rowIndex = ((floorNumber - 1) % typicalFloorRows.length) + 1; // +1 -    

    switch (patType) {
        case PatternType.REGULAR:
            {
                return typicalFloorRows[(floorNumber - 2) % typicalFloorRows.length].modules;
            }
        case PatternType.CHECKED:
            {
                if (floorNumber % 2 === 0) {
                    return typicalFloorRows[(floorNumber - 2) % typicalFloorRows.length].modules;
                }
                else {
                    var modules = typicalFloorRows[(floorNumber - 2) % typicalFloorRows.length].modules;
                    /*                     modules.push(pattern.rows[rowIndex].modules[0]);
                                        modules.shift();
                                        return modules; */
                    return shiftModules(modules);
                }
            }
        case PatternType.DOUBLECHECKED:
            {
                if (rowIndex / 2 % 2 === 0) {
                    return pattern.rows[rowIndex].modules;
                }
                else {
                    var modules = pattern.rows[rowIndex].modules;
                    modules.push(pattern.rows[rowIndex].modules[0]);
                    modules.shift();
                    return modules;
                }
            }
        case PatternType.RANDOM:
            {
                return shuffleArray(pattern.rows[rowIndex].modules);
            }
    }
}

function shiftModules(modules: FacadeModule[]) {
    var first = modules[0];
    var last = modules[modules.length - 1];

    var newList = [...modules].reverse();

    newList[0] = first;
    newList[newList.length - 1] = last;

    return newList;
}

function shuffleArray(array: any) {
    for (var i = array.length - 1; i > 0; i--) {

        // Generate random number
        var j = Math.floor(Math.random() * (i + 1));

        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }

    return array;
}

function getFacadeBlockRow(facadeModules: FacadeModule[], startPoint: THREE.Vector2, endPoint: THREE.Vector2, levelHeigth: number, floorHeight: number, coordinates: number[]): FacadeBlock[] {

    let firstFacadeBlocks: FacadeBlock[] = []
    let firstModuleWidth = 0

    let lastFacadeBlocks: FacadeBlock[] = []
    let lastModuleWidth = 0

    let patternModules: FacadeModule[] = facadeModules
    let leftEdgeModuleSource: FacadeModule = patternModules[0]

    if (facadeModules.length > 2) {

        patternModules = [...facadeModules]

        let firstModule = patternModules.splice(0, 1)[0]
        firstModuleWidth = firstModule.width
        leftEdgeModuleSource = firstModule

        let firstModuleLocations = calculateFirstModuleBottomLocations(firstModule.facadeParts, startPoint, endPoint, levelHeigth)
        firstFacadeBlocks = createLastFacadeBlocks(firstModule.facadeParts, firstModuleLocations, floorHeight, coordinates)

        let lastModule = patternModules.splice(patternModules.length - 1, 1)[0]
        lastModuleWidth = lastModule.width

        let lastModuleLocations = calculateLastModuleBottomLocations(lastModule.facadeParts, startPoint, endPoint, levelHeigth)
        lastFacadeBlocks = createLastFacadeBlocks(lastModule.facadeParts, lastModuleLocations, floorHeight, coordinates)
    }

    let modulesWidth = getFacadeModulesWidth(patternModules)
    let facadePatternLength = startPoint.distanceTo(endPoint) - (firstModuleWidth + lastModuleWidth)
    let scaledWidths = repeatAndScale(modulesWidth, facadePatternLength)

    let patternModuleslocations = calculatePatternModulesLocation(scaledWidths, startPoint, endPoint, levelHeigth, firstModuleWidth)
    let blockRow = createFacadeBlockRow(patternModules, scaledWidths, patternModuleslocations, floorHeight, coordinates)

    let leftEdgeModuleParts = createLeftEdgeModuleParts(leftEdgeModuleSource)
    let leftEdgeModuleBottomLocation = calculateLeftEdgeModuleBottomLocations(leftEdgeModuleParts, startPoint, endPoint, levelHeigth)
    let leftEdgeFacadeBlocks = createLeftEdgeFacadeBlocks(leftEdgeModuleParts, leftEdgeModuleBottomLocation, floorHeight, coordinates)

    blockRow.unshift(...leftEdgeFacadeBlocks, ...firstFacadeBlocks)
    blockRow.push(...lastFacadeBlocks)

    return blockRow
}

function getFacadeModulesWidth(facadeModules: FacadeModule[]): number[] {

    let widths: number[] = []
    for (let facadeModule of facadeModules) {
        let moduleWidth = facadeModule.width
        widths.push(moduleWidth)
    }

    return widths
}

function calculatePatternModulesLocation(scaledModulesWidth: number[], startPoint: THREE.Vector2, endPoint: THREE.Vector2, levelHeigth: number, firstModuleWidth: number): Location[] {

    const facadeVector = endPoint.clone().sub(startPoint)
    const rotationRad = facadeVector.angle()

    let lengthFromStartToCurrentLocation = firstModuleWidth
    let previousModuleWidth = 0
    let locations: Location[] = []
    for (let currentModuleWidth of scaledModulesWidth) {

        lengthFromStartToCurrentLocation += (previousModuleWidth / 2) + (currentModuleWidth / 2)
        let relativePosition = facadeVector.clone().setLength(lengthFromStartToCurrentLocation)
        let absolutePosition = startPoint.clone().add(relativePosition)
        previousModuleWidth = currentModuleWidth

        let currentLocation: Location = {
            anchorPoint: new THREE.Vector3(absolutePosition.x, absolutePosition.y, levelHeigth),
            rotationRad: rotationRad
        }

        locations.push(currentLocation)
    }

    return locations
}

function createFacadeBlockRow(facadeModules: FacadeModule[], scaledWidths: number[], locations: Location[], floorHeight: number, coordinates: number[]): FacadeBlock[] {

    let facadeBlocks: FacadeBlock[] = []

    let moduleIndex = 0
    for (let index = 0; index < locations.length; index++) {

        let facadeModule = facadeModules[moduleIndex]
        moduleIndex = (moduleIndex + 1) % facadeModules.length

        const scaledWidth = scaledWidths[index]
        const bottomLocation = locations[index]

        let moduleCoordinates = [...coordinates, index + 1]
        let facadeBlocksPerModule = createFacadeBlockPerModulePart(facadeModule, scaledWidth, bottomLocation, floorHeight, moduleCoordinates)
        facadeBlocks.push(...facadeBlocksPerModule)
    }

    return facadeBlocks
}

function createFacadeBlockPerModulePart(facadeModule: FacadeModule, scaledWidth: number, bottomLocation: Location, floorHeight: number, coordinates: number[]): FacadeBlock[] {

    let facadeBlocks: FacadeBlock[] = []

    let modulePartLocation = bottomLocation
    for (let index = 0; index < facadeModule.facadeParts.length; index++) {
        let currentModulePart = { ...facadeModule.facadeParts[index] };
        currentModulePart.width = scaledWidth

        facadeBlocks.push(new FacadeBlock(currentModulePart, modulePartLocation, [...coordinates, index], floorHeight))
        modulePartLocation = moveLocationUp(modulePartLocation, floorHeight * currentModulePart.height)
    }

    return facadeBlocks
}

function moveLocationUp(modulePartLocation: Location, modulePartHeight: number): Location {
    let point = modulePartLocation.anchorPoint.clone()
    point.z += modulePartHeight;
    return { ...modulePartLocation, anchorPoint: point }
}

function createLeftEdgeModuleParts(sourceEdgeModule: FacadeModule): FacadeModulePart[] {

    let leftEdgeParts: FacadeModulePart[] = []
    for (let sourceEdgePart of sourceEdgeModule.facadeParts) {

        if (sourceEdgePart.type == ModulePartType.WINDOW || sourceEdgePart.type == ModulePartType.BALCONY) {
            leftEdgeParts.push(sourceEdgePart)
            continue
        }

        let edgeMaterials: { [index: string]: Material } = { "": Materials.metal }

        let sourceEdgeMaterials = sourceEdgePart.materials
        let keys = Object.keys(sourceEdgeMaterials)
        if (keys.length > 1) {
            edgeMaterials = { "": sourceEdgeMaterials[""] }
        }
        else {
            edgeMaterials = sourceEdgeMaterials
        }

        let leftEdgeModulePart: FacadeModulePart = {
            width: sourceEdgePart.depth,
            height: sourceEdgePart.height,
            depth: sourceEdgePart.depth,
            type: ModulePartType.WALL,
            subtype: facadeSubtypes.wallSimple,
            materials: edgeMaterials
        }

        leftEdgeParts.push(leftEdgeModulePart)
    }

    return leftEdgeParts
}

function calculateLeftEdgeModuleBottomLocations(leftEdgeModuleParts: FacadeModulePart[], startPoint: THREE.Vector2, endPoint: THREE.Vector2, levelHeigth: number): Location[] {

    const facadeVector = endPoint.clone().sub(startPoint)
    const rotationRad = facadeVector.angle()

    let edgeModulesBottomLocations: Location[] = []
    for (let modulePart of leftEdgeModuleParts) {

        let width = modulePart.width
        let edgeRelativeAnchor = facadeVector.clone().setLength(-Math.abs(width / 2))
        let edgeAbsoluteAnchor = startPoint.clone().add(edgeRelativeAnchor)

        let edgeModuleBottomLocation: Location = {
            anchorPoint: new THREE.Vector3(edgeAbsoluteAnchor.x, edgeAbsoluteAnchor.y, levelHeigth),
            rotationRad: rotationRad
        }

        edgeModulesBottomLocations.push(edgeModuleBottomLocation)
    }

    return edgeModulesBottomLocations
}

function createLeftEdgeFacadeBlocks(leftEdgeModuleParts: FacadeModulePart[], bottomLocations: Location[], floorHeight: number, coordinates: number[]): FacadeBlock[] {

    let facadeBlocks: FacadeBlock[] = []

    let currentLocationZ = 0
    for (let index = 0; index < leftEdgeModuleParts.length; index++) {

        const modulePart = leftEdgeModuleParts[index];

        let currentBottomLocation = bottomLocations[index]
        let modulePartLocation = moveLocationUp(currentBottomLocation, currentLocationZ)

        if (modulePart.type == ModulePartType.WINDOW || modulePart.type == ModulePartType.BALCONY) {
            currentLocationZ += floorHeight * modulePart.height
            continue
        }

        facadeBlocks.push(new FacadeBlock(modulePart, modulePartLocation, [...coordinates, 0, 0], floorHeight))
        currentLocationZ += floorHeight * modulePart.height
    }

    return facadeBlocks
}

function calculateLastModuleBottomLocations(lastModuleParts: FacadeModulePart[], startPoint: THREE.Vector2, endPoint: THREE.Vector2, levelHeigth: number): Location[] {

    const facadeVector = endPoint.clone().sub(startPoint)
    const rotationRad = facadeVector.angle()

    let edgeModulesBottomLocations: Location[] = []
    for (let modulePart of lastModuleParts) {

        let width = modulePart.width
        let edgeRelativeAnchor = facadeVector.clone().setLength(-Math.abs(width / 2))
        let edgeAbsoluteAnchor = endPoint.clone().add(edgeRelativeAnchor)

        let edgeModuleBottomLocation: Location = {
            anchorPoint: new THREE.Vector3(edgeAbsoluteAnchor.x, edgeAbsoluteAnchor.y, levelHeigth),
            rotationRad: rotationRad
        }

        edgeModulesBottomLocations.push(edgeModuleBottomLocation)
    }

    return edgeModulesBottomLocations
}

function createLastFacadeBlocks(lastModuleParts: FacadeModulePart[], bottomLocations: Location[], floorHeight: number, coordinates: number[]): FacadeBlock[] {

    let facadeBlocks: FacadeBlock[] = []

    let currentLocationZ = 0
    for (let index = 0; index < lastModuleParts.length; index++) {

        const modulePart = lastModuleParts[index];

        let currentBottomLocation = bottomLocations[index]
        let modulePartLocation = moveLocationUp(currentBottomLocation, currentLocationZ)

        facadeBlocks.push(new FacadeBlock(modulePart, modulePartLocation, [...coordinates, 0, 0], floorHeight))
        currentLocationZ += floorHeight * modulePart.height
    }

    return facadeBlocks
}

function calculateFirstModuleBottomLocations(firstModuleParts: FacadeModulePart[], startPoint: THREE.Vector2, endPoint: THREE.Vector2, levelHeigth: number): Location[] {

    const facadeVector = endPoint.clone().sub(startPoint)
    const rotationRad = facadeVector.angle()

    let modulesBottomLocations: Location[] = []
    for (let modulePart of firstModuleParts) {

        //     ,    ,   
        let width = modulePart.width
        let edgeRelativeAnchor = facadeVector.clone().setLength(width / 2)
        let edgeAbsoluteAnchor = startPoint.clone().add(edgeRelativeAnchor)

        let edgeModuleBottomLocation: Location = {
            anchorPoint: new THREE.Vector3(edgeAbsoluteAnchor.x, edgeAbsoluteAnchor.y, levelHeigth),
            rotationRad: rotationRad
        }

        modulesBottomLocations.push(edgeModuleBottomLocation)
    }

    return modulesBottomLocations
}

export function GetInitialCameraPosition(facadeLayout: FacadeLayout): { target: Vector3, position: Vector3 } {
    let maxX = -Infinity;
    let minX = Infinity;
    let maxY = -Infinity;
    let minY = Infinity;
    let maxZ = -Infinity;
    let minZ = Infinity;
    facadeLayout.facadeBlocks.forEach(fb => {
        if (maxX < fb.position.x) maxX = fb.position.x;
        if (minX > fb.position.x) minX = fb.position.x;
        if (maxY < fb.position.y) maxY = fb.position.y;
        if (minY > fb.position.y) minY = fb.position.y;
        if (maxZ < fb.position.z) maxZ = fb.position.z;
        if (minZ > fb.position.z) minZ = fb.position.z;
    });
    let target = new Vector3((maxX + minX) / 2, (maxY + minY) / 2, (maxZ + minZ) / 2);
    let direction = new THREE.Vector3(-1, 0.5, 0.5);
    let position = target.clone().add(direction.clone().normalize().multiplyScalar((maxZ - minZ) * 2));
    return { target: target, position: position };
}