import React from 'react';

function DetailColumn(props) {
    const update = () => {
        props.setDetail(props.detail.Clone());

        }
    const remove = () => {
        props.current.Delete();
        props.detail.selection=props.detail.rows.find(row => row.rowType === "TypicalFloor").columns[0].details[0];

        update();
    }

    const add = () => {
        if( props.detail.rows?.find(row => row.IsSelected(props.detail)).rowType != "TypicalFloor")
        {
          props.detail.selection=props.detail.rows.find(row => row.rowType === "TypicalFloor").columns[0].details[0];
        }
        props.current.AddColumn(props.detail);
        update();
    }

    return (
        <div style={{ width: `${props.width}rem`, height: `${props.height}rem`, left: `${props.current.IsLast()?4.7:0 }rem` }} className='detailColumn'>
            {props.children}
            {props.current.CanAddColumn() &&
                <button className='add addCol' onClick={add}>
                    <span className="material-icons"> add </span>
                </button>
            }
            {(props.current.IsSelected(props.detail) && props.current.CanDelete(props.detail))&&
                <button id='fa-bottom-center' className='floatingAction' onClick={remove}>
                    <span className="material-icons"> delete </span>
                </button>
            }
        </div>
    );
}

export default DetailColumn;