import React from 'react';
import PatternButton from './PatternButton'

function PatternPicker({ images, patternType, handleClick}) {
    return (
        <div id='patternpicker'>
            <p style={{ position: 'relative', color: 'white', marginBottom: '0rem', marginTop:'-0.5rem' }}>  : </p>
            <div id='roll'>
                {images.map((image, index) => {
                    return (<PatternButton key={index} isSelected={index === patternType} image={image} clickAction={handleClick} />);
                }) }
            </div>
        </div>
    );
}

export default PatternPicker;