import React from 'react';

function DetailRow(props) {
    const update = () => 
    {
    props.setDetail(props.detail.Clone());
    }

    const remove = () => {
       
        props.current.Delete(props.detail);
        props.detail.selection=props.detail.rows.find(row => row.rowType === "TypicalFloor").columns[0].details[0];

        update();
    }

    const add = () => {
        if( props.detail.rows?.find(row => row.IsSelected(props.detail)).rowType != "TypicalFloor")
        {
          props.detail.selection=props.detail.rows.find(row => row.rowType === "TypicalFloor").columns[0].details[0];
        }
        props.current.AddRow(props.detail);
        update();
    }

    return (
        <div style={{ width: `${props.width+0.7}rem`, height: `${props.height}rem` }} className='detailRow'>
            {props.current.CanAddRow(props.detail) &&
                <button className='add addRow' onClick={add}>
                    <span className="material-icons"> add </span>
                </button>
            }
            {(props.current.IsSelected(props.detail) && props.current.CanDelete(props.detail)) &&
                <button id='fa-left-center' className='floatingAction' onClick={remove}>
                    <span className="material-icons"> delete </span>
                </button>
            }
            {props.children}
        </div>
    );
}

export default DetailRow;