import React, { useEffect, useRef, useState } from 'react';
import * as THREE from 'three';
import * as LIBRARY from '../components/three/Library';
import './editor.css';
import './detail.css';
import './sidebar/styleEditor/MyModal/MyModal.css'
import './sidebar/styleEditor/MyInputRange/MyInputRange.css'
import { OrbitControls } from './three/OrbitControls';
import { GetInitialCameraPosition } from '../core/model/FacadeLayout';

/** @type{{ initialized: boolean, scene: THREE.Scene, renderer: THREE.Renderer, camera: THREE.Camera, orbit: OrbitControls, frameId:string }} */
const initialState = {
    initialized: false,
    scene: null,
    renderer: null,
    camera: null,
    orbit: null,
    frameId: null
}

function Editor(props) {
    const [data, setData] = useState({ isFirst: true });
    const [loaded, setLoaded] = useState(false);
    const [view, setView] = useState(initialState);
    var mount = useRef(null);

    function start() {
        if (!view.frameId) view.frameId = requestAnimationFrame(animate);
    }

    function stop(frameId) {
        cancelAnimationFrame(view.frameId);
    }

    function animate() {
        if (view.initialized) view.renderer.render(view.scene, view.camera);
        view.frameId = window.requestAnimationFrame(animate);
    }

    function onResize() {
        if (view.initialized) {
            let width = window.innerWidth / 2;
            let height = window.innerHeight;
            if (height < 1) height = 1;
            view.camera.aspect = width / height;
            view.renderer.setSize(width, height);
            view.camera.updateProjectionMatrix();
        }
    }

    function cleanUp(domElement, ref, frameId) {
        stop(frameId);
        ref?.current?.removeChild(domElement);
    }

    useEffect(() => LIBRARY.InitializeLibrary(() => setLoaded(true)), []);

    useEffect(() => {
        if (view.initialized) {
            mount.current.appendChild(view.renderer.domElement);
            animate();
            onResize();
            window.addEventListener('resize', onResize);
            start();
            if (props.facadeLayout !== null && data.isFirst) {
                var cp = GetInitialCameraPosition(props.facadeLayout);
                view.camera.position.set(cp.position.x, cp.position.y, cp.position.z);
                view.camera.lookAt(cp.target.x, cp.target.y, cp.target.z);
                view.orbit.target = cp.target;
                data.isFirst = false;
            }
            if (props.facadeLayout !== null && loaded) LIBRARY.CreateGeometry(props.facadeLayout, view.scene);
        }
        else {
            var scene = new THREE.Scene();
            scene.background = new THREE.Color("#6e6d73");
            // RENDERER
            var renderer = new THREE.WebGLRenderer({ antialias: true, autoSize: true });
            renderer.setClearColor(new THREE.Color(0xE5E5E5));
            // CAMERA
            var camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 10, 1000000000);
            camera.up.set(0, 0, 1);
            //LIGHTS
            var directionalLight = new THREE.DirectionalLight(0xFFFFEB, 0.5);
            directionalLight.position.set(-500000, 500000, 100000);
            scene.add(directionalLight);
            scene.add(new THREE.AmbientLight(0xCEE9FF, 0.7));
            //
            setView({ initialized: true, scene: scene, renderer: renderer, camera: camera, orbit: new OrbitControls(camera, renderer.domElement) });
        }
        return () => cleanUp(mount, view.renderer?.domElement, view.frameId);
    }, [view, props.facadeLayout, loaded]);

    return (
        <div id='editor'>
            <div id='webgl' ref={mount} />
        </div>
    );
}

export default Editor;