import React, { useEffect, useState } from 'react';
import DetailPicker from './sidebar/DetailPicker';
import StyleEditor from './sidebar/StyleEditor';
import * as DETAILS from '../core/viewmodel/Details';
import { pattern_1floor_parapet, pattern_2modules_2part } from '../core/data/testFacadePatterns'
import { generatePattern, Pattern } from '../core/model/Pattern';
import { FacadeModule, FacadeModulePart, FacadeRow } from '../core/model/Interfaces';
import { Materials } from '../core/data/Materials';
import { facadeSubtypes } from '../core/data/facadeSubtypes';
import { ModulePartType } from '../core/Enums';
import './Sidebar.css'
import { RandomButton } from './RandomButton';

function Sidebar(props) {
    const [pattern, setPattern] = useState(pattern_1floor_parapet());
    const [detail, setDetail] = useState(DETAILS.DetailBlock.fromPattern(pattern));
    const [modulePart, setModulePart] = useState({
        depth: 500,
        height: 1,
        materials: { "": Materials.windowSimple_Black },
        subtype: facadeSubtypes.windowSingle,
        type: ModulePartType.WINDOW,
        width: 2000
    } as FacadeModulePart)
    const [lockHeightSlider, setLockHeightSlider] = useState(false);

    //      <setDetail()>
    useEffect(() => {
        //console.log("__useEffect detail", detail)
        const toPattern = (detailBlock: DETAILS.DetailBlock) => {
            let pattern = {
                rows: detailBlock.rows.map(row => {
                    let facadeModules = row.columns.map(col => {
                        let fps = col.details.map(det => {return {...det.properties}}) as FacadeModulePart[];
                        fps.forEach(fp => fp.height *= 0.01) //   
                        let facadeModule = {                            
                            facadeParts: fps.reverse()
                        } as FacadeModule;
                        facadeModule.width = facadeModule.facadeParts[0].width;
                        return facadeModule;
                    });
                    let facadeRow = {
                        modules: facadeModules,
                        rowType: row.rowType
                    } as FacadeRow
                    return facadeRow;
                }).reverse()
            } as Pattern
            return pattern;
        }

        if (detail) {
            //console.log("__useEffect pattern", toPattern(detail))
            props.setPatternAction(toPattern(detail));
        }
    }, [detail]);

    function handleDetailChange(detailBlock: DETAILS.DetailBlock) {
        setDetail(detailBlock);
        if (detailBlock.selection != null) {
            setModulePart(detailBlock.selection.properties);
            findCountElemntsOnSelected(detailBlock);
        }

    }

    function handleModulePartChange(nextModulePart: FacadeModulePart) {
        let updatedDetailBlock = detail.Clone();
        let changeWidth = nextModulePart.width != modulePart.width
        let changeHeight = nextModulePart.height != modulePart.height
        updatedDetailBlock = DETAILS.DetailBlock.updateDetailFromModulePart(updatedDetailBlock, nextModulePart, changeWidth, changeHeight)
        setDetail(updatedDetailBlock);
        setModulePart(nextModulePart);
    }

    function findCountElemntsOnSelected(detailBlock) {
        detailBlock.rows.forEach(r => r.columns.forEach(c => {
            if (c.IsSelected(detailBlock)) {
                setLockHeightSlider(c.details.length === 1);
            }
        }))
    }

    function handlerRandomButton(): void{
        //        :
            let newPattern = generatePattern()
            console.log('___generate pattern',newPattern)
            let detailBlock = DETAILS.DetailBlock.fromPattern(newPattern);
            handleDetailChange(detailBlock)
    }

    return (
        <div id='sidebar'>

            <RandomButton generatePattern={handlerRandomButton}/>
            <DetailPicker detail={detail} setDetail={handleDetailChange} />

            <StyleEditor modulePart={detail.selection?.properties} handleModulePartChange={handleModulePartChange} lockHeightSlider={lockHeightSlider} />
        </div>
    );
}

export default Sidebar;