import { facadeSubtypes } from "../data/facadeSubtypes";
import { Materials } from "../data/Materials";
import { ModulePartType } from "../Enums";
import { FacadeModulePart, FacadeSubtype, Material } from "../model/Interfaces";
import { Pattern } from "../model/Pattern";
import {v4 as uuidv4} from 'uuid';

const spacer = 0.0;

export class DetailBlock {
    rows: DetailRow[];
    email: string;
    selection: Detail | null;

    constructor() {
        /** @type {DetailRow[]} */
        this.rows = [];

        /** @type {string} */
        this.email = '';

        /** @type {Detail} */
        this.selection = null;
    }

    Clone() {
        return Object.assign(new DetailBlock(), this);
    }

    Translate(size: number) {
        
        let lengthRows=this.rows.filter(row => row.rowType === "TypicalFloor").length;
        let x = (lengthRows * 110)+(lengthRows * 15);
        if (x < 150) x = 150;
        return size / x;
    }

    ClearSelection() {
        this.selection = null;
    }

    static GetRandom(): DetailBlock {
        var block = new DetailBlock();
        block.rows = [DetailRow.GetRow(block, 3500), DetailRow.GetRow(block, 3000), DetailRow.GetRow(block, 3000)];
        block.selection = null;
        return block;
    }

    static fromPattern(pattern: Pattern) {
        let detailBlock = new DetailBlock()
        detailBlock.rows = pattern.rows.map(pr => {
            let row = DetailRow.GetNewRow(detailBlock);
            row.rowType = pr.rowType;
            row.columns = pr.modules.map(module => {
                let column = DetailColumn.GetNewColumn(row, module.width, detailBlock)
                column.details = module.facadeParts.map(fp => {
                    let detail = new Detail(column, fp.width, fp.height * 100);
                    detail.properties.materials = fp.materials
                    detail.properties.subtype = fp.subtype
                    detail.properties.type = fp.type
                    detail.properties.depth=300;
                    return detail;
                })

                //    
                column.details.reverse()
                return column;
            })

            switch (row.rowType) {
                case "FirstFloor":
                    row.height = 5200
                    break;
                case "Parapet":
                    row.height = 1200
                    break;
                default:
                    row.height = 3000
            }

            return row;
        })

        // selection
        detailBlock.selection = detailBlock.rows.filter(row => row.rowType === "TypicalFloor")[0].columns[0].details[0];

        return detailBlock;

    }

    //  ,         .  updateDetailFromModulePart is not a function
    static updateDetailFromModulePart(detailBlock: DetailBlock,
        modulePart: FacadeModulePart, changeWidth: boolean, changeHeight: boolean) {
        if (detailBlock.selection === null) {
            throw Error("    !")
        }

        detailBlock.selection.properties = modulePart;
        if (detailBlock.selection.properties.height < 1.1) {
            detailBlock.selection.properties.height *= 100;
        }

        if (changeWidth) {
            DetailBlock.updateWidthDetailesFromModulePart(detailBlock, modulePart.width);
        }
        if (changeHeight) {
            DetailBlock.updateHeightDetailesFromModulePart(detailBlock, modulePart.width);
        }
        return detailBlock;
    }


    static updateWidthDetailesFromModulePart(detailBlock: DetailBlock, width: number) {
        detailBlock.rows.forEach(m => m.columns.forEach(c => {
            if (c.IsSelected(detailBlock) === true) {

                c.details.forEach(detail => {
                    detail.properties.width = width;

                });
                c.width = width;
            }

        }))
    }
    static updateHeightDetailesFromModulePart(detailBlock: DetailBlock, height: number) {
        detailBlock.rows.forEach(m => m.columns.forEach(c => {
            if (c.IsSelected(detailBlock) === true) {
                const detailSelect = detailBlock.selection;
                let changedHeight = 100;
                c.details.map(c => changedHeight -= c.properties.height);

                var indexSelected = c.details.indexOf(detailSelect!);
                let nextIndex = indexSelected - 1;

                if (indexSelected == 0) {
                    nextIndex += 2;
                }
                const changedDetail = c.details[nextIndex];

                if (changedDetail.properties.height + changedHeight > 4) {
                    c.details[nextIndex].properties.height = (changedDetail.properties.height + changedHeight);
                } else {
                    c.details[nextIndex].properties.height = 5;
                    let newSumValue = 100;
                    c.details.map(c => newSumValue -= c.properties.height);
                    c.details[indexSelected].properties.height = c.details[indexSelected].properties.height + newSumValue;
                }
            }
        }))
    }
}

export class DetailRow {
    columns: DetailColumn[];
    height: number;
    parent: DetailBlock;
    rowType: "TypicalFloor" | "FirstFloor" | "Parapet";


    constructor(parent: DetailBlock, columns: DetailColumn[] = [], rowType: "TypicalFloor" | "FirstFloor" | "Parapet" = "TypicalFloor") {
        this.columns = columns;
        this.height = 3000;
        this.parent = parent;
        this.rowType = rowType;
    }

    GetWidth(detail: DetailBlock) {
        var length = 0;
        this.columns.forEach(c => { length += c.width });
        return detail?.Translate(length) + this.columns.length * spacer + spacer;
    }

    GetHeight(detail: DetailBlock): number {
        //console.log("___height",detail?.Translate(this.height) + spacer);
        return detail?.Translate(this.height) + spacer ;
    }

    CanAddRow(detail: DetailBlock): boolean {
        let typicalRows = detail.rows.filter(row => row.rowType === "TypicalFloor");
        return typicalRows.length < 4 && typicalRows.indexOf(this) === 0;
    }

    CanDelete(detail: DetailBlock): boolean {
        return detail.rows.filter(row => row.rowType === "TypicalFloor").length > 1;
    }

    IsSelected(detail: DetailBlock): boolean {
        var selected = false;
        this.columns.forEach(c => {
            if (c.IsSelected(detail)) selected = true;
        })
        return selected;
    }

    AddRow(detail: DetailBlock) {
        if (detail.rows.filter(row => row.rowType === "TypicalFloor").length < 4) {
            var newCollection = [DetailRow.GetNewRow(detail)];
            // console.log(newCollection[0].columns);
            detail.rows.forEach(r => {
                newCollection.push(r);
            });
            detail.rows = newCollection;
        }
    }

    Delete(detail: DetailBlock) {
        var newCollection: DetailRow[] = [];
        detail.rows.forEach(r => {
            if (r !== this) {
                newCollection.push(r);
            }
        });
        detail.rows = newCollection;
    }

    static GetRow(parent: DetailBlock, somethingElse: number): DetailRow {
        var row = new DetailRow(parent);
        row.columns = [DetailColumn.GetColumn(row, 1500), DetailColumn.GetColumn(row, 3500)];
        if (Math.random() > 0.5) row.columns.push(DetailColumn.GetColumn(row, 2500));
        return row;
    }

    static GetNewRow(parent: DetailBlock): DetailRow {
        var row = new DetailRow(parent);
       
        const copyColumns=parent.rows.find(c=>c.IsSelected(parent))?.columns;
        row.columns = copyColumns?.map(c=> {return DetailColumn.GetNewColumn2(row, c.width, parent, c.details)})!;
        return row;
    }
}

export class DetailColumn {
    width: number;
    details: Detail[];
    parent: DetailRow;

    constructor(row: DetailRow, width: number, details: Detail[] = []) {
        this.width = width;
        this.details = details;
        this.parent = row;
    }

    Delete() {
        var newCollection: DetailColumn[] = [];
        this.parent.columns.forEach(c => {
            if (c !== this) {
                newCollection.push(c);
            }
        });
        this.parent.columns = newCollection;
    }
    GetWidth(detail: DetailBlock): number {
        // console.log(detail?.Translate(this.width));
        return detail?.Translate(this.width);
    }

    IsSelected(detail: DetailBlock): boolean {
        var selected = false;
        this.details.forEach(d => {
            if (d.IsSelected(detail)) selected = true;
        })
        return selected;
    }
    IsLastSelectedIndex(detail: DetailBlock): boolean{
        var rowSelected=detail.rows.find(r=>r.IsSelected(detail))!;
        var columnSelected=rowSelected?.columns.find(c=>c.IsSelected(detail));
        var index=rowSelected?.columns.indexOf(columnSelected!);
        return (rowSelected.columns.length-1)!=index
    }
    IsLast(): boolean{
        var index=this.parent.columns.indexOf(this);
        return (this.parent.columns.length-1)===index;
    }
    IsFirst(): boolean{
        var index=this.parent.columns.indexOf(this);
        return 0!==index;
    }

    CanDelete(detail: DetailBlock): boolean {
        return this.parent.columns.length > 3 && this.IsLastSelectedIndex(detail) && this.IsFirst();
    }

    AddColumn(detail: DetailBlock) {
        const lenghtColumns=this.parent.columns.length;
        //console.log("___AddColumn");
        if (lenghtColumns < 5) {
            var rowSelected=detail.rows.find(r=>r.IsSelected(detail))!;
            var columnSelected=rowSelected?.columns.find(c=>c.IsSelected(detail));
            const copyColumn=DetailColumn.GetNewColumn2(this.parent, columnSelected?.width!, detail, columnSelected?.details!);
            this.parent.columns.splice((lenghtColumns-1), 0, copyColumn);
        }
    }

    static GetColumn(row: DetailRow, width: number): DetailColumn {
        var column = new DetailColumn(row, width);
        column.details = (Math.random() > 0.5) ? [new Detail(column, width, 100)] : [new Detail(column, width, 5), new Detail(column, width, 95)];
        if ((Math.random() > 0.7)) column.details = [new Detail(column, width, 30), new Detail(column, width, 40), new Detail(column, width, 30)];
        return column;
    }

    static GetNewColumn(row: DetailRow, width: number, parent: DetailBlock): DetailColumn {
        var column = new DetailColumn(row, width);

        column.details = [new Detail(column, width, 100)];
        // column.details =copyDetails?.map(d=>{return new Detail(column, width, d.properties.height)})!;
        return column;
    }
    static GetNewColumn2(row: DetailRow, width: number, parent: DetailBlock, details:Detail[]): DetailColumn {
        
        var column = new DetailColumn(row, width);
        //
        column.details =details?.map(d=>
            {
                const copyDetail=new Detail(column, width, d.properties.height);
                copyDetail.properties.type=d.properties.type;
                copyDetail.properties.subtype=d.properties.subtype;
                copyDetail.properties.materials=d.properties.materials;
                copyDetail.properties.depth=d.properties.depth;
                copyDetail.id=uuidv4();
                return copyDetail;
            });

        return column;
    }

    CanAddColumn(): boolean {
        return this.parent.columns.length < 5 && this.parent.columns.indexOf(this) === this.parent.columns.length - 2;
    }
}

export interface DetailProperties {
    type: ModulePartType;
    subtype: FacadeSubtype;
    width: number;
    height: number;
    depth: number;
    materials: { [index: string]: Material };
}

export class Detail {
    parent: DetailColumn;
    properties: FacadeModulePart;
    id: string|null;

    constructor(column: DetailColumn, width: number, height: number) {
        this.parent = column;
        this.properties = {
            type: ModulePartType.WALL,
            subtype: facadeSubtypes.wallSimple,
            width: width,
            height: height,
            depth: 0,
            materials: { '': Materials.brick_clinker }
            
        }
        this.id=uuidv4(); 
    }

    Select(detail: DetailBlock) {
        detail.selection = this;
    }

    GetHeight(detail: DetailBlock): number {
        var value = detail.Translate(this.properties.height / 100 * this.parent.parent.height) - (spacer);
        return value;
    }

    GetWidth(detail: DetailBlock): number {
        return detail.Translate(this.properties.width);
    }

    CanCut(): boolean {
        return this.properties.height >= 10;
    }

    CanDelete(): boolean {
        return this.parent.details.length > 1;
    }

    IsSelected(detail: DetailBlock): boolean {
        return detail.selection === this;
    }

    Cut(detail: DetailBlock) {
        //console.log('Detail Cut() method!!!')
        var newCollection: Detail[] = [];
        this.parent.details.forEach(d => {
            if (d === this) {
                var copy = new Detail(this.parent, this.parent.width, this.properties.height / 2);
                newCollection.push(copy);
                copy.Select(detail);
                var copy2 = new Detail(this.parent, this.parent.width, this.properties.height / 2);
                newCollection.push(copy2);
            }
            else {
                newCollection.push(d);
            }
        });
        this.parent.details = newCollection;
    }

    Delete(detail: DetailBlock) {
        var newCollection: Detail[] = [];
        var addNext = 0;
        var previous: Detail | null = null;

        this.parent.details.forEach(d => {
            if (d === this) {
                if (previous !== null) {
                    previous.properties.height += d.properties.height;
                    previous.Select(detail);
                }
                else {
                    addNext += d.properties.height;
                }
            }
            else {
                if (addNext !== 0) {
                    d.properties.height += addNext;
                    d.Select(detail);
                    addNext = 0;
                }
                newCollection.push(d);
            }
            previous = d;
        });
        this.parent.details = newCollection;

    }
}