export default function repeatAndScale(widths: number[], totalLength: number) {
    let sum = widths[0]
    let index = 0
    let nextWidth = widths[0]
    let repeatedWidths : number[] = []
    
    while(sum <= totalLength){
        repeatedWidths.push(nextWidth)

        index = (index + 1) % widths.length
        nextWidth = widths[index]
        sum += nextWidth        
    }
    if(sum != totalLength)
    {
        sum -= nextWidth
    }

    let scale = totalLength / sum
    let scaledWidths = repeatedWidths.map(w => w * scale)
    return scaledWidths
}