import { Object3D } from "three";
import { Rhino3dmLoader } from 'three/examples/jsm/loaders/3DMLoader';

var loader = new Rhino3dmLoader();
loader.setLibraryPath('https://cdn.jsdelivr.net/npm/rhino3dm@0.15.0-beta/');

/**
 *     (  ModuleParts)
 */
export const facadePartGeometries = {
    wallSimple: 'wall-simple',
    wallSlopedRight: 'wallSlopedRight',
    wallSlopedLeft: 'wallSlopedLeft',
    wallSlopedTop: 'wallSlopedTop',
    wallSlopedBottom: 'wallSlopedBottom',
    window: 'window',
    windowType3: 'windowType3',
    windowType3s: 'windowType3s',
    windowCircle: 'windowCircle',
    balconySimple: 'balcony-simple',
    balconyComplex: 'balcony-complex',
    firstFloorW: 'firstFloorSGW',
    firstFloorWnW: 'firstFloorSGWnW',
    parapet: 'parapet',
} as const

/**
 *       Object3d
 * @param fileName     .3dm
 * @returns Object3d
 */
function readObjectFromRhino(fileName: string) {
    var model = new Object3D()
    var path = `3dm/${fileName}.3dm`;
    loader.load(path, function (element) {
        model.children.push(element);
        model.name = element.name;
    }, undefined, undefined);

    return model
}