import React from "react";
import { FacadeModulePart, Material } from "../../core/model/Interfaces";

export default function DetailTexture({ props }: { props: FacadeModulePart }) {

    let images = Object.keys(props.materials).map((key, index) => {
        if (key !== "") {
            let material = props.materials[key];
            let paddingStyle: any = {};
            let height = 100
            let width = 100
            let margins = props.subtype.geometryParts[index].margins;
            if (margins) {
                // let paddingValue = props.subtype.geometryParts[index].margins?.map(m => m * 100).join("% ");
                // paddingStyle.padding = paddingValue;
                
                if (margins[0]) {
                    paddingStyle.top = (margins[0] * 100) + '%';
                    height -= margins[0] * 100;
                }

                if (margins[1]) {
                    paddingStyle.right = (margins[1] * 100) + '%';
                    width -= margins[1] * 100;
                }

                if (margins[2]) {
                    paddingStyle.bottom = (margins[0] * 100) + '%';
                    height -= margins[2] * 100;
                }

                if (margins[3]) {
                    paddingStyle.left = (margins[3] * 100) + '%';
                    width -= margins[3] * 100;
                }                
            }

            paddingStyle.width = width + '%'
            paddingStyle.height = height + '%'


            if (material.scalable) {
                return (
                    <img key={index}
                        src={material.textureURL}
                        className="detail-image"
                        style={paddingStyle}
                    ></img>
                );
            } else {
                //    ,    
                return (
                    <div key={index}
                        className="material"
                        style={{
                            ...paddingStyle,
                            background: `url("${material.textureURL}") center center repeat`,
                        }}
                    ></div>
                );
            }
        }
    })

    return (
        <>
            {images}
        </>
    );
}
