import React from 'react';
import { Button } from 'reactstrap';
import "./PatternButton.css"

function PatternButton({isSelected, image, clickAction}): JSX.Element {
    const cl = "pattern-type-btn" + (isSelected ? " pattern-type-btn-selected" : "")
    return (
        <Button className={cl} onClick={() => clickAction(image)}>
            <img src={image} alt="image" />
        </Button>
    );
}

export default PatternButton;