import React, { useState } from 'react';
import { Materials } from '../../../core/data/Materials';
import { FacadeSubtype, Material } from '../../../core/model/Interfaces';
import MyModal from './MyModal/MyModal';
import './MaterialSelector.css'



interface SelectorMaterial{
    subType : FacadeSubtype,
    onSelection : any
    materials: any
}

const MaterialSelector = ({subType, materials, onSelection}:SelectorMaterial) => {
    
   
    const [materialImages, setMaterialImages]=useState<Material[]>();
    const [materialName, setMaterialName]=useState();
    const [selectedIndex, setSelectedIndex]=useState<number>();
    const showModal = (key:any) => {

        const materialSources=subType.geometryParts.filter(g=>g.meshName==key)[0].materialSources;
        const materialsToModal= materialSources.map(mt => Object.values(Materials).filter(m => m.materialType === mt)).reduce((acc, el) => acc.concat(el), []);

        setMaterialImages(materialsToModal);
        setMaterialName(key);
        // 
        setSelectedIndex(materialsToModal.indexOf( materials[key]));
        toggleModal();
      };
    

      const [isOpen, setIsOpen] = useState(false);

      function toggleModal() {
        setIsOpen(!isOpen);
      }


    return (
        <div className='materialSelector'>
          <div className='rectangleBackground'></div>
          <div className='btnsMaterialSelector'>
            {Object.keys(materials).map((key: any, index : number) => {
                if(materials[key].iconURL===null ||materials===null )return;
                return (
                <div  key={index+"groupLabelMaterial"} className='materialLable'> 
                <button 
                    id='btnImage'
                    className='myButtonMaterial'
                    key={index}
                    name={key +"_btn"} onClick={() =>showModal(key)}
                    style={ {backgroundImage: `url(${materials[key].iconURL})`, 
                    backgroundSize: 'cover', 
                    backgroundRepeat: 'repeat', 
                    backgroundPosition: 'center',
                    }}
            >
             </button>
             <label id='textImage'> {key}</label>
             </div>
             )})}
             </div>
            
           
            <MyModal  materials={materialImages} 
            isOpen={isOpen} 
            toggleModal={toggleModal} 
            selectMaterialType={materialName} 
            onSelection={onSelection}
            selectedIndex={selectedIndex}/> 

          
                   
        </div>
    );
};

export default MaterialSelector;