import { Object3D, Vector3 } from "three";
import { FacadeModulePart, Location, Material } from "./Interfaces";

export class FacadeBlock {
    geometry: string;
    textures: { meshName: string, url: string, scalable: boolean }[]
    position: Vector3;

    /**     */
    rotation: number;
    scaleX: number;
    scaleY: number;
    scaleZ: number;
    coordinates: number[];

    constructor(facadeModulePart: FacadeModulePart, location: Location, coordinates: number[], floorHeight = 3000, originalModelSizeX = 3000, originalModelSizeY = 3000, originalModelSizeZ = 3000) {
        this.geometry = facadeModulePart.subtype.geometry;

        this.textures = Object.keys(facadeModulePart.materials).map((key) => {
            let mat = facadeModulePart.materials[key];
            let texture = {
                meshName: key,
                url: mat.textureURL,
                scalable: mat.scalable
            }

            return texture;
        });

        this.position = location.anchorPoint;
        this.rotation = location.rotationRad;
        this.scaleX = facadeModulePart.width / originalModelSizeX;
        this.scaleY = facadeModulePart.depth / originalModelSizeY;
        if (this.scaleY === 0) this.scaleY = 0.01;
        this.scaleZ = facadeModulePart.height * floorHeight / originalModelSizeZ;
        this.coordinates = coordinates;
    }
}

