import { DepthFormat } from "three";
import { ModulePartType } from "../Enums";
import { Author, FacadeModule, FacadeModulePart, FacadeRow, FacadeSubtype, Material } from "./Interfaces";
import { facadeSubtypes } from "../../core/data/facadeSubtypes";
import { Materials } from "../data/Materials";


export class Pattern {
    rows: FacadeRow[];
    name: string | null;
    id: string | null;
    author: Author | null;
    hblockIds: string[] | null;

    constructor(name: string, rows: FacadeRow[], author: Author | null = null, id: string | null = null, hblockIds: string[] | null = []) {
        this.rows = rows
        this.author = author
        this.id = id
        this.name = name
        this.hblockIds = hblockIds
    }
}

export function generatePattern(): Pattern {
    let rows: FacadeRow[] = []
    let rowTypes = ["FirstFloor", "TypicalFloor", "Parapet"]

    let firstRow: FacadeRow = {
        rowType: "FirstFloor",
        modules: generateModules("FirstFloor")
    }
    rows.push(firstRow)

    for (let rowCount = 0; rowCount< randomFromInterval(1,3, null); rowCount++){
        let row: FacadeRow = {
            rowType: "TypicalFloor",
            modules: generateModules("TypicalFloor")
        }
        rows.push(row)
    }

    let parapetRow: FacadeRow = {
        rowType: "Parapet",
        modules: generateModules("Parapet")
    }
    rows.push(parapetRow)

    let resultPattern: Pattern = {
        rows: rows,
        name: null,
        id: null,
        author: null,
        hblockIds: null
    }

    console.log("___generatePattern", resultPattern)
    return resultPattern
}

function generateModules(floorType): FacadeModule[] {
    let modules: FacadeModule[] = []
    let moduleCount:number = 0

    switch (floorType) {
        case "FirstFloor":
            moduleCount = 1
            break;
        case "TypicalFloor":
            moduleCount = randomFromInterval(3, 6, 1)
            break;
        case "Parapet":
            moduleCount = 1
            break;
    }


    for (let i = 0; i < moduleCount ; i++) {
        let width = randomFromInterval(300, 3000, 300)
        let flank: boolean = false
        if(i===0 || i === moduleCount-1){
            flank = true
        }

        let module: FacadeModule = {
            facadeParts: generateParts(width, floorType, flank),
            width: width
        }
        modules.push(module)
    }

    return modules
}

function randomFromInterval(min: number, max: number, step): number { // min and max included 
    if (step == null) {
        return Math.floor(Math.random() * (max - min + 1) + min)
    }
    else {
        let x = Math.floor(Math.random() * (max - min + 1) + min)
        let y = Math.round(x / step)
        return step * y
    }
}


function generateParts(moduleWidth: number, floorType: string, flank: boolean): FacadeModulePart[] {
    let modulePartsCount: number = 0

    let parts: FacadeModulePart[] = []

    //typical floor
    let maintypes: ModulePartType[] = []
    switch (floorType) {
        case "FirstFloor":
            maintypes = [ModulePartType.FIRSTFLOOR]
            modulePartsCount = 1
            break;
        case "TypicalFloor":
            if(flank){
                maintypes = [ModulePartType.WALL]
            }
            else{maintypes = [ModulePartType.WALL, ModulePartType.WINDOW, ModulePartType.BALCONY]}
            modulePartsCount = randomFromInterval(1, 3, null)
            break;
        case "Parapet":
            maintypes = [ModulePartType.PARAPET]
            modulePartsCount = 1
            break;
    }

    let heights = divideSize(modulePartsCount) //     1,  0,05

    for (let y = 0; y < modulePartsCount; y++) {


        let type = maintypes[randomFromInterval(0, maintypes.length - 1, null)]

        let allSubtypesKeys = Object.keys(facadeSubtypes).filter(k => facadeSubtypes[k].modulePartType === type)
        let subtype = facadeSubtypes[allSubtypesKeys[randomFromInterval(0, allSubtypesKeys.length - 1, null)]] as FacadeSubtype

        let material: { [index: string]: Material } = {}
        let geometryParts = subtype.geometryParts.forEach(gp => {
            let availableMaterials = Object.keys(Materials).filter(k => {
                let mat = Materials[k];
                return gp.materialSources.includes(mat.materialType)
            })

            material[gp.meshName] = Materials[availableMaterials[randomFromInterval(0, availableMaterials.length - 1, null)]]
        })

        let part: FacadeModulePart = {
            width: moduleWidth,
            height: heights[y],
            depth: randomFromInterval(0, 450, 150),
            // to do:   /    
            type: type,
            subtype: subtype,
            materials: material
        }
        parts.push(part)
    }

    return parts
}

function divideSize(modulePartsCount: number): number[] {
    let heights: number[] = []
    if (modulePartsCount === 1) {
        heights.push(1)
    }

    else {
        let allHeights = Array.from({ length: 20 }, (_, i) => i + 1)
        let heightsFromStart = pickRandom(allHeights, modulePartsCount - 1).sort((n1, n2) => n1 - n2)
        heightsFromStart.push(20)

        heights.push(heightsFromStart[0]/20)
        if (modulePartsCount > 1) {
            for (let i = 1; i < heightsFromStart.length; i++) {
                heights.push((heightsFromStart[i] - heightsFromStart[i - 1])/20)
            }
        }
    }
    return heights
}

function fisherYaetsShuffle(array) {
    let currentIndex = array.length, randomIndex;

    // While there remain elements to shuffle.
    while (currentIndex !== 0) {

        // Pick a remaining element.
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
            array[randomIndex], array[currentIndex]];
    }

    return array;
}

const range = (start, stop, step = 1) =>
    Array(Math.ceil((stop - start) / step)).fill(start).map((x, y) => x + y * step)

function pickRandom(allWidths: number[], count: number) {
    let shuffled = fisherYaetsShuffle(allWidths)
    return shuffled.slice(0, count)
}