import React, { useState } from 'react';
import DetailComponent from './Detail';
import * as DETAILS from '../../core/viewmodel/Details';
import DetailColumn from './DetailColumn';
import DetailRow from './DetailRow';

function DetailPicker(props: { detail: DETAILS.DetailBlock; setDetail: (arg0: DETAILS.DetailBlock) => any; }) {
    //const [checked, setChecked] = useState(-1);
    // const getSize = (i) => {
    //     return props.pattern.collection[props.pattern.selectedIndex][i];
    // }

    const translate = (size) => {
        let x = 300;
        return size / x;
    }

    /** @param {DETAILS.detailRow} row */
    const getColumns = (row) => {
        return row.columns.map((col, key) => {
            return (
                <DetailColumn current={col} detail={props.detail} setDetail={props.setDetail} key={key} width={col.GetWidth(props.detail)} height={col.parent.GetHeight()}>
                    {getDetails(col)}
                </DetailColumn>)
        });
    }

    /** @param {DETAILS.detailColumn} col */
    const getDetails = (col) => {
        return col.details.map((det, key) => {
            return (
                <DetailComponent key={key} detail={props.detail} current={det} setDetail={props.setDetail} width={det.GetWidth(props.detail)} height={det.GetHeight(props.detail)} />
            )
        });
    }

    const [selectFirstFloor, setSelectFirstFloor]=useState(false);
    function deselectFirstFloOrClick(){ setSelectFirstFloor(false);}
    const handleFirstFloOrClick = (typeName:string) => {
        let typeRow =  props.detail.rows.find(r => r.rowType === typeName);
        if (typeRow) {
            props.detail.selection = typeRow.columns[0].details[0];
            props.setDetail(props.detail.Clone());
        }
    }
    function selectFirstFloorOrParapet(typeName:string):boolean{ return props.detail.rows.find(r => r.rowType === typeName)?.IsSelected(props.detail)!;}
   
    

    return (
        <div id='sideelement'>
            <div id='grid' >
                <div className='parapet'>
                    <button className='noTypeFloorBtn' id={selectFirstFloorOrParapet("Parapet") ? 'selectedNoTypeBtn' : ''}  onClick={()=>handleFirstFloOrClick("Parapet")} ></button>
                </div>
                {props.detail.rows
                    .filter(row => row.rowType === "TypicalFloor")
                    .map((row, key) => {
                        
                        return (<DetailRow key={key} detail={props.detail} setDetail={props.setDetail} current={row} width={row.GetWidth(props.detail)} height={row.GetHeight(props.detail)}>
                            {getColumns(row)}
                        </DetailRow>)
                    })}
                <div className='firstFloor'>
                    <button className='noTypeFloorBtn' id={selectFirstFloorOrParapet("FirstFloor") ? 'selectedNoTypeBtn' : ''}  onClick={()=>handleFirstFloOrClick("FirstFloor")}> </button>
                </div>
            </div>
        </div>
    );
}

export default DetailPicker;