import * as THREE from 'three';
import { Rhino3dmLoader } from 'three/examples/jsm/loaders/3DMLoader';
import { Materials } from '../../core/data/Materials';
import { facadePartGeometries } from '../../core/data/facadePartGeometries';
import { FacadeLayout } from '../../core/model/FacadeLayout';
import { Object3D } from 'three';

/** @type {Object<string, THREE.Material>} */
export const MATERIAL_LIBRARY = {}

/** @type {Object<string, THREE.Object3D>} */
export const OBJECT3D_LIBRARY = {}

/** @type {Object<string, THREE.Texture>} */
export const TEXTURE_LIBRARY = {}

/** @type {Object<string, THREE.Material>} */
export const SCENE_MATERIAL_LIBRARY = {}

/** @type {THREE.Object3D[]} */
export var SCENE_OBJECT3D_LIBRARY = []

/** @type {Object<string, THREE.Texture>} */
export const SCENE_TEXTURE_LIBRARY = {}

const load_manager = new THREE.LoadingManager();
const texture_loader = new THREE.TextureLoader(load_manager);
const rhino_3dm_loader = new Rhino3dmLoader(load_manager);
rhino_3dm_loader.setLibraryPath('https://cdn.jsdelivr.net/npm/rhino3dm@0.15.0-beta/');

/**
 * @param {() => void} callback
 */
export function InitializeLibrary(callback) {
    Object.keys(Materials).forEach((k, i) => {
        texture_loader.load(Materials[k].textureURL, (texture) => {
            TEXTURE_LIBRARY[Materials[k].textureURL] = texture;
        });
    });

    Object.keys(facadePartGeometries).forEach((k, i) => {
        rhino_3dm_loader.load(`3dm/${facadePartGeometries[k]}.3dm`, (object) => {
            OBJECT3D_LIBRARY[facadePartGeometries[k]] = object;
        });
    });

    if (callback) {
        load_manager.onLoad = () => { setTimeout(() => callback(), 1000) }
    }
}

/**
 * @param {THREE.Scene} scene
 */
export function RemoveGeometry(scene) {
    SCENE_OBJECT3D_LIBRARY.forEach(object => {
        scene.remove(object);
        object.children.forEach(c => {
            if (c.material) {
                if (SCENE_MATERIAL_LIBRARY[c.material.name]) {
                    SCENE_MATERIAL_LIBRARY[c.material.name] = null;
                    delete SCENE_MATERIAL_LIBRARY[c.material.name];
                    if (SCENE_TEXTURE_LIBRARY[c.material.name]) {
                        SCENE_TEXTURE_LIBRARY[c.material.name] = null;
                        delete SCENE_TEXTURE_LIBRARY[c.material.name];
                    }
                }
                c.material.dispose();
                c.material = null;
            }
        })
        object = null;
    });
    SCENE_OBJECT3D_LIBRARY = [];
}

/**
 * @param {FacadeLayout} facadeLayout
 * @param {THREE.Scene} scene
 */
export function CreateGeometry(facadeLayout, scene) {
    RemoveGeometry(scene);

    let roof = facadeLayout.roof.clone();
    SCENE_OBJECT3D_LIBRARY.push(roof);
    scene.add(roof);

    facadeLayout.buttCaps.forEach(bc => {
        let clone = bc.clone();
        SCENE_OBJECT3D_LIBRARY.push(clone);
        scene.add(clone);
    })

    facadeLayout.facadeBlocks.forEach(fb => {
        let textures = {};
        fb.textures.forEach(t => { textures[t.meshName] = t })
        OBJECT3D_LIBRARY[fb.geometry].children.forEach((c) => {
            let clone = new THREE.Mesh(c.geometry, GetMaterial(new THREE.Vector3(fb.scaleX, fb.scaleY, fb.scaleZ), textures[c.name]));
            clone.position.copy(fb.position);
            clone.scale.set(fb.scaleX, fb.scaleY, fb.scaleZ);
            clone.rotateZ(fb.rotation);
            SCENE_OBJECT3D_LIBRARY.push(clone);
            scene.add(clone);
        });
    })
}

/**
 * @param {THREE.Vector3} scale
 * @param {{ meshName: string, url: string, scalable: boolean }?} texture
 * @returns {THREE.Material}
 */
export function GetMaterial(scale, texture) {
    let code = 'NOT FOUND';
    if (!texture) {
        if (!MATERIAL_LIBRARY[code]) {
            MATERIAL_LIBRARY[code] = new THREE.MeshStandardMaterial();
            MATERIAL_LIBRARY[code].color = new THREE.Color(1.0, 0.0, 0.0);
        }
        return MATERIAL_LIBRARY[code];
    }
    code = !texture.scalable ? `${texture.url}_${Math.round(scale.x * 100) / 100}_${Math.round(scale.z * 100) / 100}` : texture.url;
    if (texture.scalable) {
        if (!MATERIAL_LIBRARY[code]) {
            MATERIAL_LIBRARY[code] = new THREE.MeshPhysicalMaterial();
            MATERIAL_LIBRARY[code].color = new THREE.Color(1.5, 1.5, 1.5);
            if (texture.scalable) {
                MATERIAL_LIBRARY[code].reflectivity = 1.0;
                MATERIAL_LIBRARY[code].metalness = 0.5;
                MATERIAL_LIBRARY[code].roughness = 0.5
            }
            MATERIAL_LIBRARY[code].map = TEXTURE_LIBRARY[code];
        }
        return MATERIAL_LIBRARY[code];
    }
    else {
        if (!SCENE_MATERIAL_LIBRARY[code]) {
            SCENE_MATERIAL_LIBRARY[code] = new THREE.MeshPhysicalMaterial();
            SCENE_MATERIAL_LIBRARY[code].color = new THREE.Color(1.5, 1.5, 1.5);
            if (!SCENE_TEXTURE_LIBRARY[code]) {
                SCENE_TEXTURE_LIBRARY[code] = TEXTURE_LIBRARY[texture.url].clone();
                SCENE_TEXTURE_LIBRARY[code].repeat.set(scale.x, scale.z);
                SCENE_TEXTURE_LIBRARY[code].wrapS = THREE.RepeatWrapping;
                SCENE_TEXTURE_LIBRARY[code].wrapT = THREE.RepeatWrapping;
            }
            SCENE_MATERIAL_LIBRARY[code].map = SCENE_TEXTURE_LIBRARY[code];
        }
        return SCENE_MATERIAL_LIBRARY[code];
    }
}